







import { Component, Provide, Vue } from 'vue-property-decorator';
@Component({})
export default class App extends Vue {
    private isRouterAlive: boolean = true;

    @Provide('reload')
    p_reload = this.reload;

    created() {}

    reload() {
        this.isRouterAlive = false;
        this.$nextTick(() => {
            this.isRouterAlive = true;
        });
    }
}
