import Vue from 'vue';
import Router, { RawLocation, Route, RouteConfig, RouterOptions } from 'vue-router';
import { Position, PositionResult } from 'vue-router/types/router';
import store from '../store';
import LocalStorage from '@/service/localStorage';
const login = () => import('../views/login/index.vue');
const teacher = () => import('../views/teacher/index.vue');
const startClass = () => import('../views/teacher/startClass.vue');
const studentMobile = () => import('../views/studentPhone/index.vue');
const studentMobileLive = () => import('../views/studentPhone/startLive.vue');

const studentPC = () => import('../views/studentPC/index.vue');
const studentClass = () => import('../views/studentPC/startClass.vue');

const room = () => import('../views/room/index.vue');
Vue.use(Router);

// 根据是否为移动端，跳转到不同页面
const studentGuards = (to: Route, from: Route, next: (to?: RawLocation | false | void) => void) => {
    let targetUrl = '';
    let isMobile = /(iPhone|iPad|iPod|iOS|Android)/i.test(navigator.userAgent);
    let currentUrlMobile = to.path.includes('student-m');
    let pathChange = false;

    if (!currentUrlMobile && isMobile) {
        targetUrl = to.fullPath.replace('student', 'student-m');
        // console.log(store)
        // console.log(store.state.isStart)
        if (!store || store.state.isStart == 1) {
            console.log('跳转到播放页面');
            targetUrl = to.fullPath.replace('student-m', 'pull-m');
        }
        pathChange = true;
        console.log('换成移动端');
    } else if (currentUrlMobile && !isMobile) {
        targetUrl = to.fullPath.replace('student-m', 'student');
        if (!store || store.state.isStart == 1) {
            targetUrl = to.fullPath.replace('student', 'pull');
        }
        pathChange = true;
        console.log('换成桌面端');
    } else {
        // console.log(to.fullPath)
        targetUrl = to.fullPath;
        console.log('不变');
    }

    if (pathChange) {
        next(targetUrl);
    } else {
        next();
    }
};

const routes: RouteConfig[] = [
    {
        path: '',
        redirect() {
            return '/login';
        },
    },
    {
        path: '/login',
        component: login,
    },
    {
        path: '/room',
        component: room,
        beforeEnter(to: Route, from: Route, next: (to?: RawLocation | false | void) => void) {
            const auth = LocalStorage.get('auth');
            if (auth && auth.appId) {
                next();
            } else {
                next(location.pathname.replace('room', 'login') + location.search);
            }
        },
    },
    {
        path: '/teacher/:roomId',
        component: teacher,
    },
    {
        path: '/push/:roomId',
        component: startClass,
        beforeEnter(to: Route, from: Route, next: (to?: RawLocation | false | void) => void) {
            if (!store.state.isLogin) {
                next(location.pathname.replace('push', 'teacher') + location.search);
            } else {
                next();
            }
        },
    },
    {
        path: '/student/:roomId',
        component: studentPC,
        beforeEnter: studentGuards,
    },
    {
        path: '/student-m/:roomId',
        component: studentMobile,
        beforeEnter: studentGuards,
    },
    {
        path: '/pull/:roomId',
        component: studentClass,
        beforeEnter(to: Route, from: Route, next: (to?: RawLocation | false | void) => void) {
            if (from.fullPath.includes('student')) {
                next();
            } else {
                next(to.fullPath.replace('pull', 'student'));
            }
        },
    },

    {
        path: '/pull-m/:roomId',
        component: studentMobileLive,
        beforeEnter(to: Route, from: Route, next: (to?: RawLocation | false | void) => void) {
            if (from.fullPath.includes('student-m')) {
                console.log(from.fullPath.includes('student-m'));
                next();
            } else {
                next(to.fullPath.replace('pull-m', 'student-m'));
            }
        },
    },
];

export default new Router({
    mode: process.env.VUE_APP_ROUTERMODE,
    fallback: false,
    scrollBehavior: (to: Route, from: Route, savedPosition: Position | void) => {
        return { x: 0, y: 0 };
    },
    routes: routes,
});
